@use 'sass:color' as sass-color;
@use 'sass:meta' as meta;
@use 'sass:string' as string;

@use 'badges';
@use 'bootstrap';
@use 'code';
@use 'fouce';
@use 'hljs';
@use 'mixins';
@use 'navbar';
@use 'themes/hljs/tomorrow';
@use 'toc';
@use 'variables/colors';
@use 'variables/fonts';
@use 'variables/spacers';

@mixin clearfix() {
  &::after {
    display: block;
    clear: both;
    content: '';
  }
}

:root {
  --jsdoc-copy-icon-color: var(--jsdoc-color-zinc-800);
  --jsdoc-copy-icon-color-hover: var(--jsdoc-color-blue-700);
  --jsdoc-copy-icon-opacity: 0;
  --navbar-scroll-margin: #{navbar.$height + spacers.$medium};
  --navbar-scroll-margin-large: #{navbar.$height + spacers.$xx-large};

  // Expose selected SASS variables as CSS custom properties.
  @each $module in (colors fonts spacers) {
    @each $name, $value in meta.module-variables($module) {
      // Omit the module's trailing `s` from the custom property name.
      $bucket: string.slice($module, 1, -2);
      --jsdoc-#{$bucket}-#{$name}: #{$value};
    }
  }
}

html {
  position: relative;
  height: 100%;
}

body {
  display: flex;
  flex-flow: column;
  min-height: 100%;
  overflow-y: scroll;
  color: colors.$zinc-900;
  font-family: fonts.$body-font, serif;
}

// TODO: Remove !important if we remove Bootstrap.
.link-primary,
a {
  color: colors.$sky-700 !important;
  text-decoration-color: colors.$sky-700 !important;
}

@each $level in (1, 2, 3, 4, 5, 6) {
  h#{$level},
  .h#{$level} {
    display: inline-block;
    font-family: fonts.$h-font, sans-serif;
    font-size: fonts.h-font-size($level);

    &:focus,
    &:hover {
      --jsdoc-copy-icon-opacity: 1;
    }
  }
}

h1 > small,
.h1 > small {
  font-size: calc(fonts.h-font-size(1) * 0.65);
}

h2,
.h2 {
  margin-block-end: spacers.$medium;
}

@each $level in (2, 3) {
  h#{$level},
  .h#{$level} {
    margin-block-start: fonts.$h-margin-block-start-large;
  }
}

@each $level in (2, 3) {
  .symbol-index:first-child h#{$level},
  .symbol-index:first-child .h#{$level} {
    margin-block-start: 0;
  }
}

@each $level in (4, 5, 6) {
  h#{$level},
  .h#{$level} {
    margin-block-start: fonts.$h-margin-block-start-small;
  }
}

@each $level in (1, 2) {
  h#{$level},
  .h#{$level} {
    + section {
      h#{$level + 1}:first-child,
      .h#{$level + 1}:first-child {
        margin-block-start: fonts.$h-margin-block-start-small;
      }
    }
  }
}

a {
  @extend .link-primary;
  text-decoration: none;

  :focus,
  :hover {
    text-decoration: underline;
  }
}

b,
strong {
  font-weight: fonts.$font-weight-semibold;
}

dt,
th {
  font-family: fonts.$sans-serif-font;
}

.dl-compact {
  dt {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    float: inline-start;
    width: fonts.$dl-horizontal-offset;
    clear: inline-end;
    text-align: start;
  }

  dd {
    @include clearfix;
    line-height: fonts.$line-height-computed * 0.75;
    margin-inline-start: fonts.$dl-horizontal-offset;

    p {
      line-height: fonts.$line-height-computed;
    }

    pre {
      margin-block-end: 0;
    }
  }
}

.dl-compact > dd::after {
  display: inline-block;
}

.dl-summary-callout dd {
  margin-block-end: fonts.$line-height-computed * 0.5;
}

dd > p {
  margin-block-end: 0;
}

.page-header {
  border-block-end: 0;
  margin: 0 0 spacers.$large;
  padding-block-end: spacers.$x-small;
}

.property-type {
  margin-inline-start: fonts.$font-size-base;
}

.signature-params,
.signature-returns {
  color: colors.$zinc-500;
}

.source-link {
  font-size: fonts.$font-size-base * 0.9;
  margin-block-start: calc(spacers.$x-small * -1);
}

.summary-callout,
.symbol-index-section {
  font-size: fonts.$font-size-base * 0.9;
}

.summary-callout-heading,
.symbol-index-section-heading {
  @extend .h4;
  font-size: 1.125rem;
  margin-block-start: fonts.$h-margin-block-start-small;
  border-block-end: 0;
}

// Use larger text in the first paragraph of the description
.symbol-classdesc,
.symbol-description {
  p:first-child {
    margin-block-end: fonts.$line-height-computed * 1.1;
    font-size: fonts.$font-size-base * 1.5;
    line-height: fonts.$line-height-computed * 1.5;
    font-weight: 300;
  }
}

.h.symbol-params,
.h.symbol-properties {
  font-size: fonts.h-font-size(4) * 0.9;
}

.symbol-index-list > dd {
  margin-block-end: 0.75em;
}

.symbol-index-name {
  text-indent: -1em;
  margin-inline-start: 1em;
}

.symbol-name {
  font-weight: bold;
}

.summary-callout,
.symbol-index {
  padding: 0;
}

.summary-content,
.symbol-index-section {
  padding: 0 spacers.$medium 0 0;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
}

.jsdoc-main {
  display: flex;
  flex: 1 1 auto;
  width: 100%;
  padding-block-start: navbar.$height;
}

.jsdoc-content {
  flex: 1 1 auto;
  height: 100%;
  max-width: 90ch;
  padding: spacers.$xx-large spacers.$medium spacers.$xx-large;

  // If there are badges before an element, then make sure the navbar doesn't cover the badges when
  // the browser scrolls to that element.
  .symbol-detail-badges + [id] {
    --navbar-scroll-margin: var(--navbar-scroll-margin-large);
  }
}

.jsdoc-on-this-page {
  flex: 1 1 auto;
  padding-block-start: spacers.$xx-large;
  z-index: navbar.$z-index - 2;

  // TODO: make sticky styling consistent with the left nav (or vice-versa)
  jsdoc-outline {
    position: sticky;
    top: calc(var(--navbar-scroll-margin) + spacers.$xx-large);
  }
}

.jsdoc-footer {
  align-self: end;
  justify-self: stretch;
  width: 100%;
  padding: spacers.$medium;
  background-color: colors.$zinc-100;
  border-block-start: 1px solid colors.$zinc-300;

  p {
    margin: 0;
    text-align: center;
  }
}

/* Tables */

// TODO: Remove if we get rid of Bootstrap.
.table > :not(caption) > * > *,
.jsdoc-details-table > :not(caption) > * > * {
  color: inherit;
}

.jsdoc-details-table {
  @extend .table;
}

.jsdoc-details-table > thead > tr {
  border-block-end-width: 2px;
}

.jsdoc-details-table > tbody > tr:last-child {
  border-block-end-color: colors.$white;
}

td > p:last-child {
  margin-block-end: 0;
}

article td,
article th {
  @extend .table-sm;
  padding-inline-start: 0;
}

/* Callouts */

.summary-callout {
  border-radius: 0.2rem;
  padding: 0 1em 0 1em;
  margin: 1em 0 1em 0;
  border: 1px solid colors.$zinc-300;
  border-inline-start-width: 1em;
  border-inline-start-color: colors.$sky-600;
}
